import { useNavigate } from "react-router";
import Button from "components/atoms/Button";
import { StyledTableCell, StyledTableRow } from "components/templates/Tables";
import {
  Table,
  TableRow,
  TableBody,
  TableHead,
  TableContainer,
} from "@mui/material";
import Stack from "@mui/material/Stack";
import TableLoadingWrapper from "components/templates/TableLoadingWrapper";
import { BsTrashFill } from "react-icons/bs";
import { AiOutlineEdit } from "react-icons/ai";
import { useEffect } from "react";
import { useAppDispatch, useAppSelector } from "redux/hooks";
import { MODAL } from "redux/slices/modal";
import { modalActions } from "redux/slices/modal";
import { SubCategoryService } from "services/subcategories.service";

export default function SubCategoriesList() {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const subcategories = useAppSelector(
    (state) => state.subCategory.subCategories
  );
  const loading = useAppSelector((state) => state.subCategory.loading);
  const filter = useAppSelector((state) => state.subCategory.filter);
  const refreshLoader = useAppSelector(
    (state) => state.subCategory.refreshLoader
  );

  useEffect(() => {
    SubCategoryService.getAllSubCategories(filter, dispatch);
  }, [filter, dispatch]);

  return (
    <div>
      <TableContainer>
        <Table
          aria-label="customized table"
          sx={{
            minWidth: "100%",
            borderSpacing: "0 10px",
            borderBottomWidth: "0px",
            borderCollapse: "separate",
          }}
        >
          <TableLoadingWrapper
            container
            coloumns={4}
            loading={loading}
            message="No sub-categories available"
            length={refreshLoader ? 0 : subcategories.length}
          >
            <TableHead>
              <TableRow>
                <StyledTableCell>ID</StyledTableCell>
                <StyledTableCell>Name</StyledTableCell>
                <StyledTableCell>Parent Category</StyledTableCell>
                <StyledTableCell>Published</StyledTableCell>
                <StyledTableCell></StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {subcategories?.map((category: any, i: number) => (
                <StyledTableRow key={i}>
                  <StyledTableCell>{i + 1}</StyledTableCell>
                  <StyledTableCell>{category?.name}</StyledTableCell>
                  <StyledTableCell>
                    {category?.parentCategoryId}
                  </StyledTableCell>
                  <StyledTableCell>
                    {category?.isActive === true ? "Yes" : "No"}
                  </StyledTableCell>
                  <StyledTableCell align="right">
                    <Stack direction="row" spacing={2} justifyContent="right">
                      <Button
                        variant="contained"
                        color="primary"
                        disableElevation
                        onClick={() =>
                          dispatch(
                            modalActions.openModal({
                              width: "500px",
                              type: MODAL.CONFIRMATION_FORM,
                              data: {
                                id: category._id,
                                type: MODAL.DELETE_SUBCATEGORY,
                                heading: "Delete Sub Category",
                                message:
                                  "Do you really want to delete this Sub Category?",
                              },
                            })
                          )
                        }
                        sx={{ minWidth: "auto" }}
                      >
                        <BsTrashFill />
                      </Button>
                      <Button
                        variant="contained"
                        color="secondary"
                        disableElevation
                        onClick={() =>
                          navigate(`/update-subcategory/${category._id}`)
                        }
                        sx={{ minWidth: "auto" }}
                      >
                        <AiOutlineEdit />
                      </Button>
                    </Stack>
                  </StyledTableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </TableLoadingWrapper>
        </Table>
      </TableContainer>
    </div>
  );
}
