import { fields } from ".";
import FiltersForm from "components/templates/FiltersForm";
import { subCategoryActions } from "redux/slices/subcategory";
import { reset } from "redux-form";
import { useAppDispatch } from "redux/hooks";

export default function SubCategoryFilters() {
  const dispatch = useAppDispatch();
  const form = "subCategoryFiltersForm";
  const handleSubmit = (values: any) => {
    const data = { ...values };

    if (data.pageSize) data.pageSize = Number(data.pageSize);

    dispatch(subCategoryActions.setFilter({ filters: data }));
  };

  return (
    <FiltersForm
      onSubmit={handleSubmit}
      myFields={fields}
      form={form}
      onClickReset={() => {
        dispatch(reset(form));
        dispatch(subCategoryActions.setFilter({}));
      }}
    />
  );
}
