import { Container, Stack } from "@mui/material";
import Button from "components/atoms/Button";
import Banner from "components/templates/Banner";
import BasicCard from "components/templates/BasicCard";
import { useNavigate } from "react-router-dom";
import { useAppSelector } from "redux/hooks";
import CategoriesList from "./SubCategoriesList";
import CategoryFilters from "./SubCategoryFilters";

export default function SubCategoryManagement() {
  const cat = useAppSelector((state) => state.subCategory.subCategories);

  const navigate = useNavigate();
  return (
    <Container maxWidth="lg">
      <Banner heading={`Sub Category Management (${cat.length})`}>
        <Stack direction="row" spacing={2}>
          <Button
            color="secondary"
            variant="contained"
            sx={{ borderRadius: "25px" }}
          >
            Export
          </Button>
          <Button
            color="primary"
            variant="contained"
            sx={{ borderRadius: "25px" }}
            onClick={() => navigate("/add-subcategory")}
          >
            + Add New
          </Button>
        </Stack>
      </Banner>
      <BasicCard>
        <CategoryFilters />
      </BasicCard>
      <BasicCard>
        <CategoriesList />
      </BasicCard>
    </Container>
  );
}
